const useOnScreenLandingPage = (options) => {
  const [landingRef, setLandingRef] = useState(null);
  const [isLandingVisible, setIsLandingVisible] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(([entry]) => {
      setIsLandingVisible(entry.isIntersecting);
    }, options);

    if (landingRef) {
      observer.observe(landingRef);
    }

    return () => {
      if (landingRef) {
        observer.unobserve(landingRef);
      }
    };
  }, [landingRef, options]);

  return [setLandingRef, isLandingVisible];
};

